



















































/*左侧导航进入动画*/
.slideLeft-enter[data-v-6166214c]{
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%); /*平移是从左上角开始计算的*/
}
.slideLeft-enter-active[data-v-6166214c]{
  transition: all 0.7s;
}
.slideLeft-leave-active[data-v-6166214c]{
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  transition: all 0.7s;
}
/*二级页面进入动画*/
.pageIn-enter[data-v-6166214c]{
  -webkit-transform: translate(100%,0);
          transform: translate(100%,0)
}
.pageIn-enter-active[data-v-6166214c]{
  transition: all 0.7s linear; 
  position: absolute;
  width: 100%;
}
.pageIn-leave-active[data-v-6166214c]{
  -webkit-transform: translate(-80%,0);
          transform: translate(-80%,0);
  transition: all 0.7s linear; 
  position: absolute;  
  width: 100%;
}
/*二级页面退出动画*/
.pageOut-enter[data-v-6166214c]{
  -webkit-transform: translate(-80%,0);
          transform: translate(-80%,0)
}
.pageOut-enter-active[data-v-6166214c]{
  transition: all 0.7s linear; 
  position: absolute;
  width: 100%;
  z-index: 1; /*进入的覆盖退出的*/
}
.pageOut-leave-active[data-v-6166214c]{
  -webkit-transform: translate(100%,0);
          transform: translate(100%,0);
  transition: all 0.7s linear; 
  position: absolute;  
  width: 100%;
}



